<template>
    <div class="sidebar">
        <div class="sidebar-backdrop" @click="closeSidebarPanel" v-if="isPanelOpen"></div>
        <transition name="slide">
          <nav v-if="isPanelOpen" class="sidebar-panel col-12">
            <div class="grid sidebar-inner-container">
              <h5>Socials</h5>
              <ul class="header-nav row justify-content-left">
                <li v-for="profile in profiles" :key="profile.id">
                  <a :href="`${profile.link}`" target="_blank">
                    <i :class="profile.icon"></i>
                  </a>
                </li>
              </ul>
              <h5>Links</h5>
              <div class="row">
                <button v-for="routes in links" v-bind:key="routes.id" class="sidebar-links col-12 text-left" @click="redirect(routes.page)">
                  <p>{{ routes.text }}</p>
                </button>
              </div>
            </div>
            <p class="copy">© 2020 Azad Nalbandian</p>
          </nav>
        </transition>
    </div>
</template>
<script>
import { store, mutations } from '../../store.js'

export default {
  methods: {
    closeSidebarPanel: mutations.toggleNav,
    redirect (arg) {
      this.$router.push(arg)
    }
  },
  computed: {
    isPanelOpen () {
      return store.isNavOpen
    }
  },
  data () {
    return {
      links: [
        {
          id: 0,
          text: 'Home',
          page: '/',
          icon: 'fas fa-home d-inline'
        },
        {
          id: 1,
          text: 'About',
          page: '/About',
          icon: 'fas fa-address-card'
        },
        {
          id: 2,
          text: 'Projects',
          page: '/projects',
          icon: 'fas fa-project-diagram'
        },
        {
          id: 3,
          text: 'Blog',
          page: '/blog',
          icon: 'fas fa-blog'
        }
      ],
      profiles: [
        {
          id: 0,
          link: 'https://github.com/azadnalbandian',
          icon: 'header-icon fab fa-github fa-2x'
        },
        {
          id: 1,
          link: 'https://www.linkedin.com/in/azad-nalbandian-057a4b149/',
          icon: 'header-icon fab fa-linkedin fa-2x'
        },
        {
          id: 2,
          link: 'mailto:azadnalbandian@gmail.com',
          icon: 'header-icon fas fa-at fa-2x'
        }
      ]
    }
  }
}
</script>
<style>
    .slide-enter-active,
    .slide-leave-active
    {
        transition: transform 0.2s ease;
    }

    .slide-enter,
    .slide-leave-to {
        transform: translateX(-100%);
        transition: all 150ms ease-in 0s
    }

    .sidebar-backdrop {
        background-color: rgba(0,0,0,.5);
        z-index: 998;
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        cursor: pointer;
    }

    .sidebar-panel {
        overflow-y: hidden;
        overflow-x: hidden;
        position: fixed;
        left: 0;
        top: 0;
        height: 100vh;
        width: 30vh;
        z-index: 998;
    }

    .sidebar-inner-container {
      margin-top: 80px;
    }

    .sidebar-links {
      background-color: rgba(0,0,0,0);
      margin-left: 1em;
      color: white;
      border: 0px !important;
      transition: 0.2s;
    }

  .sidebar-links:hover {
      color: lightblue;
      transform-origin: left;
  }

  .sidebar-links:active {
    color: orange;
  }

  ul.header-nav {
      list-style-type: none;
  }

  .header-icon {
      color: white;
      transition: 0.2s;
  }

  .header-icon:hover {
      color: lightblue;
      transform: scale(1.5);
  }

  .header-icon:active {
    color: orange;
  }

  .copy {
    position: fixed;
    bottom: 0;
    left: 0;
    margin-left: 1em;
    color: white;
    font-size: 12px;
  }

  h5 {
    color: rgba(255,255,255,0.5);
    font-variant: small-caps;
    margin-left: 10px;
    margin-bottom: 1em;
  }

</style>



